import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-blog-dashboard',
  templateUrl: './blog-dashboard.component.html',
  styleUrls: ['./blog-dashboard.component.css']
})
export class BlogDashboardComponent implements OnInit {

  blog: FormGroup;
  displayStyle = "none";
  constructor(private fb: FormBuilder) { }

  ngOnInit() {
    this.blog = this.fb.group(
      {
        category: [
          '',
          [Validators.required, Validators.pattern('[a-zA-Z ]*'), Validators.minLength(20)],
        ],
        article: [
          '',
          [
            Validators.required,
            Validators.email,
            Validators.pattern('^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$'),
          ],
        ],
        authorName: ['', [Validators.required, Validators.minLength(1000)]],
        blogName: ['', [Validators.required, Validators.minLength(20)]],
      },
    );
  }

  public onOpenModal(): void {
    console.log("inside open mdoal")
    this.blog = this.fb.group({}); // Assign an empty FormGroup object
    const container = document.getElementById('main-container');
    const button = document.createElement('button');
    button.type = 'button';
    button.style.display = 'none';
    button.setAttribute('data-toggle', 'modal');
    button.setAttribute('data-target', '#addTweetModal');
    container.appendChild(button);
    console.log("button ", button)
    button.click();
  }

}
